import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { IItemService } from '../@intermediate/item/item.interface';
import { AddPackageToShoppingCartRequest, AddPackageToShoppingCartResponse, GetPackageDetailRequest, GetPackageDetailResponse } from '../@intermediate/item/models/get-package-detail.model';

@Injectable({
  providedIn: 'root'
})

export class ItemService implements IItemService {
  private readonly serviceUrl: string;
  private readonly shoppingCartUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient) {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl('Retail/ECommerce/Item');
    this.shoppingCartUrl = this.apiBaseUrlProvider.getBaseUrl('Retail/ECommerce/ShoppingCart');
  }

  public GetPackageDetail(request: GetPackageDetailRequest): Observable<GetPackageDetailResponse> {
    const apiUrl = this.serviceUrl + '/GetPackageDetail';
    return this.http.get<GetPackageDetailResponse>(apiUrl, { params: request as any });
  }

  public AddPackageToShoppingCart(request: AddPackageToShoppingCartRequest): Observable<AddPackageToShoppingCartResponse> {
    const apiUrl = this.shoppingCartUrl + '/AddPackageToCart';
    return this.http.post<AddPackageToShoppingCartResponse>(apiUrl, {
      PackageCartItems: [request as any]
    });
  }
}
