import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatNativeDateModule } from '@angular/material/core';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { MatLegacyPaginatorModule as MatPaginatorModule } from '@angular/material/legacy-paginator';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { MatSortModule } from '@angular/material/sort';
import { RouterModule } from '@angular/router';
import { TeiCurrencyTranslateModule, TeiDateTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { ConfirmDialogModule, FormTimePickerModule, ImageCropDialogModule, ImageSliderModule, LoadingModule, NumberInputModule, SnackBarModule, TimePickerModule } from '@tei/material';
import { NgxMatTimepickerModule } from 'ngx-mat-timepicker';
import { ItemService } from '../../@services/item.service';
import { PackageDetailComponent } from './package-detail.component';

const CORE_MODULES = [TeiTextTranslatePipeModule, TeiCurrencyTranslateModule, TeiDateTranslateModule];
const BASE_MODULES = [CommonModule, RouterModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatIconModule, MatFormFieldModule, MatInputModule, MatButtonModule, NgxMatTimepickerModule, MatCheckboxModule,
  MatTableModule, MatPaginatorModule, MatSortModule, MatSelectModule, MatMenuModule, RouterModule, MatCardModule, MatDatepickerModule, MatNativeDateModule, MatDividerModule];
const TEI_MATERIAL_MODULES = [TeiCurrencyTranslateModule, NgxMatTimepickerModule.setLocale('en-US'), ImageSliderModule, ConfirmDialogModule, SnackBarModule, NumberInputModule, ImageCropDialogModule, TimePickerModule, FormTimePickerModule, LoadingModule];

@NgModule({
  declarations: [PackageDetailComponent],
  imports: [
    ...BASE_MODULES, ...MATERIAL_MODULES, ...TEI_MATERIAL_MODULES, ...CORE_MODULES
    , RouterModule.forChild([{ path: '', component: PackageDetailComponent }])
  ],

  providers: [{ provide: 'IItemService', useClass: ItemService }],
})
export class PackageDetailModule { }
