import { Component, Inject, Injectable, OnDestroy, OnInit, Optional } from '@angular/core';
import { UntypedFormControl } from '@angular/forms';
import { MAT_MOMENT_DATE_ADAPTER_OPTIONS, MomentDateAdapter } from '@angular/material-moment-adapter';
import { DateAdapter, MAT_DATE_LOCALE } from '@angular/material/core';
import { MatDatepicker } from '@angular/material/datepicker';
import { ActivatedRoute, Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { TranslateService } from '@ngx-translate/core';
import { IAuthService } from '@tei/abstraction';
import { ResponsiveService } from '@tei/core';
import { SnackBarService } from '@tei/material';
import moment from 'moment';
import { Subject, Subscription, finalize, firstValueFrom, takeUntil } from 'rxjs';
import { EComCartItemsRequestForm } from '../../@intermediate/cash-eq-detail';
import { IItemService } from '../../@intermediate/item/item.interface';
import { AddPackageToShoppingCartRequest, GetPackageDetailRequest, GetPackageDetailResponse } from '../../@intermediate/item/models/get-package-detail.model';
import { ShopCommon } from '../../@intermediate/shop';
import { ShoppingCartService } from '../../Components/shopping-cart/shopping-cart.service';
import { IShoppingCartItemsState } from '../../store';

@Injectable()
export class MyDateAdapter extends MomentDateAdapter
{
  constructor(
    @Inject(MAT_DATE_LOCALE) locale: string,
    @Optional() @Inject(MAT_MOMENT_DATE_ADAPTER_OPTIONS) private options?: any
  )
  {
    super(locale, options);
  }

  format(date: moment.Moment, displayFormat: string): string
  {
    if (this.locale.includes("fr"))
    {
      return date.format('ddd, MMM D, YYYY');
    } else
    {
      return date.format('ddd, MMM D, YYYY');
    }
  }

  setDateFormat(format: string)
  {
    this.setLocale(this.locale);

    this.setDateFormat(format);
  }
}

@Component({
  selector: 'tei-ecommerce-pages-package-detail',
  templateUrl: './package-detail.component.html',
  styleUrls: ['./package-detail.component.scss'],
  providers: [
    {
      provide: DateAdapter,
      useClass: MyDateAdapter,
    },
    {
      provide: MAT_DATE_LOCALE, useValue: JSON.parse(localStorage.getItem('profile-settings')) ?
        JSON.parse(localStorage.getItem('profile-settings'))?.language : 'en'
    }
  ]
})
export class PackageDetailComponent extends ShopCommon implements OnInit, OnDestroy
{
  startTime = new Date();
  endTime = new Date(1995, 10, 17, 23, 45, 0);
  model: GetPackageDetailResponse;
  form: EComCartItemsRequestForm;
  ItemUID: string;
  displayCostas: string;
  images = [];
  imageObject: string[] = [];
  confirmEmail: string;
  ItemID: string;
  value: number = 1;
  currentDate: Date = new Date();
  authenticated: boolean = false;
  isLoading = false;
  private destroyed$: Subject<void> = new Subject<void>();
  isMobile = false;

  subscription = new Subscription();

  constructor(
    private shoppingCartService: ShoppingCartService,
    @Inject('IAuthService') private authService: IAuthService,
    @Inject('IItemService') private itemService: IItemService,
    private activatedRoute: ActivatedRoute,
    private store: Store<IShoppingCartItemsState>,
    private router: Router,
    private responsiveService: ResponsiveService,
    private translateService: TranslateService,
    private snackBarService: SnackBarService

  )
  {
    super();
    this.ItemUID = this.activatedRoute.snapshot.params['eCommItemUID'];
  }

  async ngOnInit()
  {
    this.authenticated = await firstValueFrom(this.authService.isAuthenticatedOrRefresh());
    const navigateByURL = !!this.activatedRoute.snapshot.queryParams['NavigateByURL'];

    const request: GetPackageDetailRequest = {

      ItemUID: this.ItemUID,
      CallByURLOnly: navigateByURL
    };

    this.isMobile = this.responsiveService.mediaBreakpoint.getValue() === 'xs';

    this.load(request);

    let tempBeforeLoginRequest = localStorage.getItem("package_temp_request");
    if (tempBeforeLoginRequest)
    {
      let bodyRequest: AddPackageToShoppingCartRequest = JSON.parse(tempBeforeLoginRequest);
      localStorage.removeItem("package_temp_request");
      this.AddToCartAuthenticatedUser(bodyRequest);
    }
  }

  ngOnDestroy()
  {
    this.destroyed$.next();
    this.destroyed$.complete();
    this.subscription.unsubscribe();
  }

  private load(request: GetPackageDetailRequest)
  {

    if (!this.model)
    {
      this.model = new GetPackageDetailResponse();
    }

    this.itemService.GetPackageDetail(request).subscribe(response =>
    {
      this.model = response as GetPackageDetailResponse;

      if (this.model?.Pictures.length > 0)
      {
        this.model.Pictures.forEach(element =>
        {
          this.images.push(element?.PictureURL);
        });

        this.imageObject = this.images;
      }

      this.form = new EComCartItemsRequestForm(moment(this.model.AvailableStartDateTimeUtc).toDate());

      this.startTime = this.getCurrentTimeRounded();

      this.form.controls['EmailDate'].valueChanges.pipe(takeUntil(this.destroyed$)).subscribe((emailDate) =>
      {
        if (this.isAfterToday(emailDate))
        {
          this.startTime = new Date(1995, 10, 17, 0, 0, 0);
        } else
        {
          this.startTime = this.getCurrentTimeRounded();
        }
      });
    }, error =>
    {
      if (error?.error?.ErrorCode?.toString() == '400002')
      {
        this.router.navigate(['../../'], { relativeTo: this.activatedRoute });
      }
    });
  }

  private getCurrentTimeRounded(): Date
  {
    const currentTime = moment(this.model.AvailableStartDateTimeUtc);
    let currentHour = currentTime.hours();
    const currentMinute = currentTime.minutes();
    let roundedMinute = Math.ceil(currentMinute / 15) * 15;

    if (roundedMinute === 60)
    {
      roundedMinute = 0;
      currentHour++;
    }

    if (currentHour === 24)
    {
      currentHour = 0;
    }

    return new Date(1995, 10, 17, currentHour, roundedMinute, 0);
  }

  private isAfterToday(dateString: string): boolean
  {
    const date = new Date(dateString);
    return moment(date).isAfter(this.model.AvailableStartDateTimeUtc);
  }

  openDatePicker(picker: MatDatepicker<any>)
  {
    picker.open();
  }

  getDeliveryTime()
  {
    return this.translateService.instant('ECOMMERCE.CASHEQ_DETAIL.DELIVERY_TIME') + ' *';
  }

  getFormControlByName(name: string): UntypedFormControl
  {
    return this.form.get(name) as UntypedFormControl;
  }

  getTime(emailDate: Date, emailTime: Date): Date
  {

    const currentDate = moment(emailDate);
    const timeToAdd = moment(emailTime, 'HH:mm');
    const resultDate = currentDate.clone().set({
      hour: timeToAdd.hour(),
      minute: timeToAdd.minute(),
      second: 0,
      millisecond: 0
    });

    return resultDate.toDate();

  }

  getId(id: number)
  {
    this.form.controls['Id'].setValue(id);
  }

  onAddToCart()
  {
    if (this.form.invalid) return;

    if (!this.authenticated)
    {
      // Redirect unauthenticated users to login with current path as redirect parameter
      let requestBody = this.getRequestPostBody();
      localStorage.setItem("package_temp_request", JSON.stringify(requestBody));

      const currentPath = this.router.url;
      this.router.navigate(['/auth/login'], {
        queryParams: { redirect_path: currentPath }
      });
      return;
    }

    this.AddToCartAuthenticatedUser();
    this.shoppingCartService.onAddToShoppingCart();
  }

  getShopBaseRoute()
  {
    const currentUrl = this.router.url;
    const urlParts = currentUrl.split('/').filter(Boolean);
    const targetParts = urlParts.slice(0, urlParts.length - 2);
    const targetUrl = '/' + targetParts.join('/');

    return targetUrl;
  }

  AddToCartAuthenticatedUser(request: AddPackageToShoppingCartRequest | null = null): void
  {
    this.isLoading = true;

    if (request == null)
      request = this.getRequestPostBody();

    this.subscription.add(this.itemService.AddPackageToShoppingCart(request).pipe(finalize(() =>
    {
      this.isLoading = false;
    })).subscribe(response =>
    {
      if (response.IsSuccess)
      {
        this.snackBarService.showSuccess(this.translateService.instant("ECOMMERCE.CASHEQ_DETAIL.ADDED_TO_CART"), '', null, 'left');
        this.shoppingCartService.onAddToShoppingCart();
        localStorage.setItem("PaymentEngineReturnURL", this.getShopBaseRoute());
      }
    }));
  }

  getRequestPostBody(): AddPackageToShoppingCartRequest
  {
    const sendAsGift = this.form.controls.SendAsGift.value;
    return {
      Quantity: 1,
      ItemID: this.model.ItemID,
      ItemUID: this.model.ItemUID,
      ItemCode: this.model.ItemCode,
      RecipientEmail: this.form.get("RecipientEmail").value,
      SendAsGift: sendAsGift,
      ItemType: this.model.ItemTypeCode,
      PackageCode: this.model.PackageCode,
      PackageAmount: this.model.PackagePrice,
      InstanceID: this.model.InstanceID,
      ...(sendAsGift && {
        GiftTo: this.form.get('GiftTo').value,
        GiftFrom: this.form.get('GiftFrom').value,
        GiftMessage: this.form.get('GiftMessage').value,
        EmailDeliveryDateUtc: this.form.get('EmailDate').value,
        EmailDeliveryTimeUtc: this.getTime(this.form.get('EmailDate').value, this.form.get('EmailTime').value),
      })
    };
  }
}
